<template>
  <div class="listDetail">
    <mHeader go-back='true' ref="child1" head-title='订单详情'></mHeader>
    <div class="detailContent">
      <div class="detailPlan">
        <span>活动计划{{$route.params.bid}}</span>
        <ul>
          <li>
            <em>主题：</em>
            <em><input type="text" v-model="orderList.theme" disabled></em>
          </li>
          <li>
            <em>宾客人数：</em>
            <em><input type="text" v-model="orderList.guests" disabled></em>
          </li>
          <li>
            <em>时间：</em>
            <em><input type="text" v-model="orderList.use_time" disabled></em>
          </li>
          <li>
            <em>预算范围：</em>
            <em><input type="text" v-model="orderList.budget_price" disabled></em>
          </li>
        </ul>
      </div>
      <div class="detailPlan">
        <span>活动场地</span>
        <div class="mapContent">
          <div class="map"></div>
          <p><input type="text" v-model="orderList.address" disabled></p>
        </div>
      </div>
      <div class="detailPlan">
        <span>活动信息</span>
        <div class="activeBus">
          <p><input type="text" v-model="orderList.information" disabled></p>
        </div>
      </div>
      <div class="detailPlan">
        <span>联络人</span>
        <ul>
          <li>
            <em><input type="text" v-model="orderList.contact_name" disabled></em>
          </li>
          <li>
            <em>微信：</em>
            <em><input type="text" v-model="orderList.contact_wechat" disabled></em>
          </li>
          <li>
            <em>电话：</em>
            <em><input type="text" v-model="orderList.contact_phone" disabled></em>
          </li>
          <li>
            <em>邮箱：</em>
            <em><input type="text" v-model="orderList.contact_email" disabled></em>
          </li>
        </ul>
      </div>
      <div class="detailPlan">
        <span>订单信息</span>
        <ul>
          <li>
            <em>创建时间：</em>
            <em><input type="text" v-model="orderList.create_time" disabled></em>
          </li>
          <li>
            <em>订单号：</em>
            <em><input type="text" v-model="orderList.order_sn" disabled></em>
          </li>
          <!--<li>
            <em>已付定金：</em>
            <em>2018年10月24日</em>
          </li>-->
          <!--<li class="pay">
            <em>待付尾款：</em>
            <em>18k-28k</em>
          </li>-->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  export default {
    created(){
      this.achieveList();
    },
    data(){
      return{
        orderList:{},
      }
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    watch:{
      beforeRouteLeave(to,from,next){
        console.log(to,from,next)
        next();
      }
    },
    methods:{
      handleScroll () {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      },
      achieveList(){
        this.$post('/feast_v1/order/detail',{
          order_sn:this.bid
        }) .then((response) => {
          if(response.error_code==10000){
            let dataList = response.data;
            // alert(JSON.stringify(dataList+'aaaaaa'));
            this.orderList = dataList;
          }else{
            this.$toast({
              duration: 1500,
              forbidClick: true, // 禁用背景点击
              message: response.message,
            });
          }
        })
      }
    },
    components:{
      mHeader
    },
    computed: {
      bid(){
        return this.$route.params.bid; // 将路径参数id映射到bid id上
      }
    },
  }
</script>

<style lang="scss" scoped>
  @import "../../../style/mixin";
  .listDetail{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: .88rem;
    z-index: 102;
    /*overflow-y: scroll;*/
    background: #070705;
    input{
      background: none;
      border:none;
      @include sc(.26rem, #fff)
    }
    .detailContent{
      height: 100%;
      overflow-y: scroll;
      .detailPlan{
        span{
          @include fjc;
          background: rgba(22,22,17,0.7);
          @include sc(.3rem, #d3c9b3);
          height: 1.04rem;
        }
        ul{
          padding: .3rem .8rem .4rem .8rem;
          li{
            @include fjs;
            height: .42rem;
            em{
              font-style: normal;
              @include sc(.26rem, #969696);
              text-align: justify;
              display: inline-block;
            }
            em:nth-child(1){
              padding-right: .1rem;
              width: 1.4rem;
              text-align: justify;
            }
          }
          .pay{
            em{
              color: #d4be8c;
            }
          }
        }
        .mapContent{
          margin: .4rem;
          .map{
            width: 100%;
            height: 2.3rem;
            background: #999;
            margin-bottom: .1rem;
          }
          p{
            @include sc(.26rem, #969696);
            padding-left: .4rem;
          }
        }
        .activeBus{
          margin: .4rem;
          p{
            @include sc(.26rem, #969696)
          }
        }
      }
    }
  }

</style>



